/* src/styles/carousel.css */

.carousel-container {
    width: 90%;
    margin: 40px 0;
    color: #fff;
  }
  
  .carousel-container h2 {
    margin-bottom: 20px;
    font-size: 1.8rem;
  }
  
  .influencers-carousel {
    display: flex;
    overflow-x: auto;
    scroll-behavior: smooth;
    gap: 20px;
    padding-bottom: 10px;
  }
  
  .carousel-wrapper {
    display: flex;
  }
  
  .influencer-card {
    min-width: 200px;
    flex: 0 0 auto;
    background-color: #2e2a45;
    border-radius: 10px;
    overflow: hidden;
    text-align: center;
    transition: transform 0.3s ease;
  }
  
  .influencer-card:hover {
    transform: scale(1.05);
  }
  
  .profile-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .influencer-name {
    padding: 10px 0;
    font-size: 1.1rem;
    color: #fff;
  }
  