/* src/styles/home.css */

:root {
    --color-background: #1f1b2d;
    --color-card-background: #2e2a45;
    --color-highlight: #6a4686; /* Morado */
    --color-primary: #28a745; /* Verde */
    --color-secondary: #e74c3c; /* Rojo */
    --color-text: #fff;
    --color-shadow: rgba(0, 0, 0, 0.3);
    --color-card-shadow: rgba(174, 123, 255, 0.2); /* Sombra morada */
  }
  
  .home-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 20px;
    background-color: var(--color-background);
    color: var(--color-text);
    min-height: 100vh;
  }