/* src/styles/featuredInfluencer.css */

.featured-influencer {
    display: flex;
    align-items: center;
    background-color: #2e2a45;
    padding: 30px;
    margin: 30px 0;
    border-radius: 15px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .featured-influencer:hover {
    transform: scale(1.02);
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.5);
  }
  
  .featured-image img {
    width: 300px;
    height: 300px;
    object-fit: cover;
    border-radius: 50%;
    margin-right: 30px;
  }
  
  .featured-info {
    flex: 1;
  }
  
  .featured-name {
    font-size: 2rem;
    color: #fff;
    margin-bottom: 10px;
  }
  
  .featured-bio {
    font-size: 1rem;
    color: #ccc;
    margin-bottom: 20px;
  }
  
  .follow-btn {
    padding: 10px 20px;
    background-color: #ff6f6f;
    color: #fff;
    text-decoration: none;
    border-radius: 25px;
    transition: background-color 0.3s ease;
  }
  
  .follow-btn:hover {
    background-color: #ff4c4c;
  }
  