/* src/styles/secondaryInfluencer.css */

.secondary-influencer {
    display: flex;
    align-items: center;
    background-color: #3a2d5e;
    padding: 20px;
    margin: 20px 0;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .secondary-influencer:hover {
    transform: scale(1.02);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.5);
  }
  
  .featured-image img {
    width: 200px;
    height: 200px;
    object-fit: cover;
    border-radius: 50%;
    margin-right: 20px;
  }
  
  .featured-info {
    flex: 1;
  }
  
  .featured-name {
    font-size: 1.5rem;
    color: #fff;
    margin-bottom: 5px;
  }
  
  .featured-bio {
    font-size: 0.9rem;
    color: #ccc;
    margin-bottom: 15px;
  }
  
  .follow-btn {
    padding: 8px 16px;
    background-color: #ff6f6f;
    color: #fff;
    text-decoration: none;
    border-radius: 20px;
    transition: background-color 0.3s ease;
  }
  
  .follow-btn:hover {
    background-color: #ff4c4c;
  }
  